.input {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.input label {
  font-weight: 500;
  margin-left: auto;
  margin-right: 0.75rem;
}

.input input {
  font: inherit;
  padding-left: 0.5rem;
  width: 3rem;
  border-radius: 0.3rem;
  border: 0.1rem solid #cfcece;
}